//

.keyboard {
    border: 1px solid black;
    display: inline-block;
    .highlight {
        background: lime;
    }
}

//

@keyframes rotating {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.rotating {
    animation: rotating 2s linear infinite;
}
